




















import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Test1 extends Vue {
  @Prop() private msg?: string;
  private selected: string = "";
  private noSelected: boolean = false;
  private yesSelected: boolean = false;
  private yesSeletedTimeOut: boolean = false;

  private options: any = [
    { text: "Không", value: "0" },
    { text: "Có", value: "1" }
  ];

  private yesClick() {
    this.noSelected = false;
    this.yesSelected = true;
    this.yesSeletedTimeOut = false;

    setTimeout(() => {
      this.yesSelected = false;
      this.yesSeletedTimeOut = true;
      window.open('https://www.google.com/search?sxsrf=ACYBGNQ-HjuBpDwS82Zrhu8Wi9Xo8rAG6g%3A1574042836400&ei=1PzRXfWKGIG5mAX9vIGgAw&q=c%C3%A1ch+ch%E1%BB%AFa+b%E1%BB%87nh+ngu&oq=c%C3%A1ch+ch%E1%BB%AFa+b%E1%BB%87nh+ngu&gs_l=psy-ab.3..0l3j0i22i30l7.6568.10404..10669...13.4..6.144.3219.14j17......0....1..gws-wiz.......0i71j35i39j0i67j0i131.571_4DM9cx8&ved=0ahUKEwj1xP_A1vLlAhWBHKYKHX1eADQQ4dUDCAs&uact=5','_blank');
    }, 3000);
  }

  private noClick() {
    this.$notify.error({
      title: "Error",
      message: "Đáp án không được chấp nhận!"
    });
    this.noSelected = true;
    this.yesSelected = false;
    this.yesSeletedTimeOut = false;
    this.selected = "";
  }
}
